@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
body {
    margin: 0;
    background: -webkit-gradient(linear, right top, left top, from(rgb(0, 255, 179)), color-stop(80%, rgba(0, 80, 250, 1)));
    background: -webkit-linear-gradient(right, rgb(0, 255, 179) 0%, rgba(0, 80, 250, 1) 80%);
    background: linear-gradient(to left, rgb(0, 255, 179) 0%, rgba(0, 80, 250, 1) 80%);
}
div, h2 {
    font-family: 'Righteous', cursive;
    font-weight: 200;
}

h1 {
    background: -webkit-gradient(linear, right top, left top, from(rgb(226, 0, 0)), to(#333));
    background: -webkit-linear-gradient(right, rgb(226, 0, 0) 0%, #333);
    background: linear-gradient(to left, rgb(226, 0, 0) 0%, #333);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

input{
    background: transparent;
    color: white;
}

